.navbar {
  background: rgb(30, 30, 30);
  height: 5rem;
  width: 100vw;
  max-width: 75rem;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 3;
}
.desktopMenu
{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.logo {
  margin-right: 5rem;
  object-fit: cover;
  height: 3rem;
  width: 4rem;
  /* justify-content: center; */
  cursor: pointer;
  
}

.desktopMenuListItem {
  margin-right: 5rem;
  cursor: pointer;
  justify-content: center;
  display: flex;
  align-items: center;
  /* flex-grow: 0; */
  
}
.desktopMenuListItem:hover {
  margin-left: 0rem;

  color: yellow;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid yellow;
}

.desktopMenuButton {
  background: white;
  color: black;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  border-radius: 2rem;
  cursor: pointer;
}
.desktopMenuImg {
  object-fit: cover;
  height: 1rem;
  width: 1rem;
  margin: 1rem;
}



.navMenu {
  
  position: absolute;
  top: 4rem;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  cursor: pointer;
  height: fit-content;
  min-width: 15rem;
  background: rgb(40,40,40);
  border-radius: 1rem;
  
}

.listItem {
  color: white;
  padding: .5rem 3rem;
  margin: .25rem;
  background: rgb(30,30,30);

}

.mobMenu {
  display: none;
  object-fit: cover;
  height: 1.8rem;
  cursor: pointer;
}

@media screen and (max-width: 720px){
  .mobMenu {
    display: flex;
  }
  .desktopMenu{
    display: none;
  }

  .desktopMenuButton{
    display: none;
  }
}
